* {
  box-sizing: border-box;
}

a {
  &,
  &:hover {
    color: white;
    text-decoration: none;
  }
}

title {
  text-transform: capitalize;
}

*:focus {
  outline: none;
}

body {
  font-family: "Open Sans", sans-serif;
  margin: 0;
  padding: 0;
  background-color: #f6f6f6;
}
